@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.outside {
  width: 100%;
}
@media screen and (min-width: 768px) {
  .overview__contents--inner {
    width: 100%;
    background-color: #333;
  }
}
@media screen and (min-width: 768px) {
  .overview__contents {
    padding-right: 5.625rem;
  }
}
.overview__inner {
  width: 100%;
  background-color: #111;
}
.overview__title {
  max-width: 23.4375rem;
  width: 100%;
  display: inline-block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.45;
  color: #fff;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .overview__title {
    display: inline-block;
    max-width: 80rem;
    width: 100%;
    font-size: 1.875rem;
    font-weight: 400;
    letter-spacing: 0.03125rem;
    line-height: 1;
    text-align: center;
  }
}
.overview__title::before {
  content: "Overview";
  display: block;
  color: #f9f871;
  opacity: 0.27;
  font-family: "Noto Serif JP", serif;
  font-size: 3.125rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.44;
}
@media screen and (min-width: 768px) {
  .overview__title::before {
    font-size: 6.875rem;
    letter-spacing: 0.125rem;
    line-height: 1.4363636;
  }
}
.overview {
  max-width: 80rem;
  width: 100%;
  margin: 0 auto;
}